  --cf-font-family-serif: "Iowan Old Style", "Apple Garamond", Baskerville, "Times New Roman", "Droid Serif", Times, "Source Serif Pro", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --cf-font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --cf-root-font-size: 2rem;
  --cf-body-font-family: var(--cf-font-sans-serif);
  --cf-body-font-size: 1rem;
  --cf-body-font-weight: 400;
  --cf-body-line-height: 1.5;
  --cf-body-color: #13171a;
  --cf-body-color-rgb: to-rgb(#13171a);
  --cf-body-bg: #fff;
  --cf-body-bg-rgb: to-rgb(#fff);
  --cf-heading-color: ;
  --cf-link-color: #0055e9;
  --cf-link-color-rgb: to-rgb(#0055e9);
  --cf-link-decoration: underline;
  --cf-link-hover-color: #00338c;
  --cf-link-hover-color-rgb: to-rgb(#00338c);
  --cf-code-color: #bf2012;
  --cf-highlight-color: #13171a;
  --cf-highlight-bg: #ff0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  font-size: var(--cf-root-font-size);
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

body {
  margin: 0;
  font-family: var(--cf-body-font-family);
  font-size: var(--cf-body-font-size);
  font-weight: var(--cf-body-font-weight);
  line-height: var(--cf-body-line-height);
  color: var(--cf-body-color);
  text-align: var(--cf-body-text-align);
  background-color: var(--cf-body-bg);
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

hr {
  margin: 1rem 0;
  color: inherit;
  border: 0;
  border-top: 3px solid;
  opacity: 0.25;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: 600;
  line-height: 1.25;
}

h1, .h1 {
  font-size: calc(1.15rem + 1.8vw);
}
@media (min-width: 75em) {
  h1, .h1 {
    font-size: 2.5rem;
  }
}

h2, .h2 {
  font-size: calc(1.1rem + 1.2vw);
}
@media (min-width: 75em) {
  h2, .h2 {
    font-size: 2rem;
  }
}

h3, .h3 {
  font-size: calc(1.075rem + 0.9vw);
}
@media (min-width: 75em) {
  h3, .h3 {
    font-size: 1.75rem;
  }
}

h4, .h4 {
  font-size: calc(1.05rem + 0.6vw);
}
@media (min-width: 75em) {
  h4, .h4 {
    font-size: 1.5rem;
  }
}

h5, .h5 {
  font-size: calc(1.025rem + 0.3vw);
}
@media (min-width: 75em) {
  h5, .h5 {
    font-size: 1.25rem;
  }
}

h6, .h6 {
  font-size: 1rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-cfw-tooltip-original-title],
abbr[data-cfw-popover-original-title] {
  text-decoration: underline dotted;
  cursor: help;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul {
  padding-left: 2rem;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small, .small {
  font-size: 0.875em;
}

mark, .mark {
  padding: 0.125em 0.25em;
  color: var(--cf-highlight-color);
  background-color: var(--cf-highlight-bg);
}

sub,
sup {
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: rgba(var(--cf-link-color-rgb), var(--cf-link-opacity, 1));
  text-decoration: underline;
}
a:hover {
  --cf-link-color-rgb: var(--cf-link-hover-color-rgb);
}

a:not([href]):not([class]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):not([class]), a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: var(--cf-font-monospace);
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  font-size: 0.875em;
}
pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}

code {
  font-size: 0.875em;
  color: var(--cf-code-color);
  word-wrap: break-word;
}
a > code {
  color: inherit;
}

kbd {
  padding: 0.125rem 0.375rem;
  font-size: 0.875em;
  color: #eff1f3;
  background-color: #13171a;
  border-radius: 0.1875rem;
}
kbd kbd {
  padding: 0;
  font-size: 1em;
}

figure {
  margin: 0 0 1rem;
}

img,
svg {
  vertical-align: middle;
}

table {
  caption-side: bottom;
  border-collapse: collapse;
}

caption {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  color: #5e7182;
  text-align: left;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

thead,
tbody,
tfoot,
tr,
th,
td {
  border-color: inherit;
  border-style: solid;
  border-width: 0;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus:not(:focus-visible) {
  outline: 0;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
}

[role=button] {
  cursor: pointer;
}

select {
  word-wrap: normal;
}
select:disabled {
  opacity: 1;
}

[list]:not([type=date]):not([type=datetime-local]):not([type=month]):not([type=week]):not([type=time])::-webkit-calendar-picker-indicator {
  display: none !important;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}
button:not(:disabled),
[type=button]:not(:disabled),
[type=reset]:not(:disabled),
[type=submit]:not(:disabled) {
  cursor: pointer;
}

::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  width: 0.875rem;
  height: 0.875rem;
}

textarea {
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  float: left;
  width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: calc(1.05rem + 0.6vw);
  line-height: inherit;
}
@media (min-width: 75em) {
  legend {
    font-size: 1.5rem;
  }
}
legend + * {
  clear: left;
}

progress {
  vertical-align: baseline;
}

::-webkit-datetime-edit-fields-wrapper,
::-webkit-datetime-edit-text,
::-webkit-datetime-edit-minute,
::-webkit-datetime-edit-hour-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-year-field {
  padding: 0;
}

::-webkit-inner-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::file-selector-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

iframe {
  border: 0;
}

summary {
  display: list-item;
  cursor: pointer;
}

[hidden] {
  display: none !important;
}

.lead {
  font-size: calc(1.025rem + 0.3vw);
  font-weight: 600;
  line-height: 1.5;
}
@media (min-width: 75em) {
  .lead {
    font-size: 1.25rem;
  }
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.initialism {
  font-size: 0.875em;
  text-transform: uppercase;
}

.blockquote {
  margin: 0 0 1rem;
  font-size: calc(1.025rem + 0.3vw);
  color: #13171a;
}
@media (min-width: 75em) {
  .blockquote {
    font-size: 1.25rem;
  }
}
.blockquote > :last-child {
  margin-bottom: 0;
}

.blockquote-footer {
  margin: -1rem 0 1rem;
  font-size: 0.875rem;
  color: #5e7182;
}
.blockquote-footer::before {
  content: "— ";
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.img-thumbnail {
  padding: 0.25rem;
  background-color: #fff;
  border: 3px solid #9eaab4;
  border-radius: 0.375rem;
  max-width: 100%;
  height: auto;
}

.figure {
  display: inline-block;
}

.figure-img {
  margin-bottom: 0.5rem;
  line-height: 1;
}

.figure-caption {
  font-size: 0.875em;
  color: #5e7182;
}

.customfont {
  font-size: calc(1.1rem + 1.2vw);
}
@media (min-width: 75em) {
  .customfont {
    font-size: 2rem;
  }
}

.customcolor {
  color: #ffcccc;
  background: #eff1f3;
}

